.js-anim {
	$zero: 0;
	$value: 4rem;
	transform: translate($zero, $value);
	opacity: $zero;
	transition: transform .8s, opacity .8s;
	@media (max-width: 479px) {
		opacity: 1;
		transform: none;
		transition: none; }
	&--opacity {
		transform: translate($zero,$zero); }
	&--left {
		transform: translate(-$value, $zero); }
	&--right {
		transform: translate($value, $zero); }
	&--top {
		transform: translate(0, -$value); }
	&--d-02 {
		transition-delay: .2s; }
	&--d-04 {
		transition-delay: .4s; }
	&--d-06 {
		transition-delay: .6s; }
	&--d-08 {
		transition-delay: .8s; } }
.js-mouse-parallax {
	transition: transform .1s linear;
	@media (max-width: 1023px) {
		transform: translate(0,0) !important; } }
@keyframes addBodyScroll {
  to {
    overflow: visible; } }
